import imutable from 'seamless-immutable'

const initialState = imutable([
    {
        to: '/',
        name: 'Главная',
        description: 'Мойка, защитная пленка, нанокерамика, полировка, ремонт кожи пластика, химчистка',
        keywords: 'cleantonn'
    }, {   
        
        isDropdown: true,
        name: 'Услуги',
        DropdownItems: [
            {
                to: '/service/wash',
                alt: 'Мойка кузова',
                name: 'Мойка'
            }, {
                to: '/service/himchistka',
                alt: 'Химчистка салона',
                name: 'Химчистка'
            }, {
                to: '/service/polish',
                alt: 'Полировка кузова',
                name: 'Полировка'
            }, {
                to: '/service/nano',
                alt: 'Нанокерамика',
                name: 'Нанокерамика'
            }, {
                to: '/service/paint',
                alt: 'Оклейка плёнкой',
                name: 'Защитная плёнка'
            }, {
                to: '/service/repair',
                alt: 'Реставрация кожи',
                name: 'Ремонт кожи, пластика'
            }
        ]
    }, {
        to: '/advantages',
        name: 'Наши недостатки',
        description: 'Удивлять чистотой и качеством сервиса — наш главный ПРИНЦИП!!!',
        keywords: 'customKeywords'
    }, {
        to: '/rewiews',
        name: 'Ваши благодарности'
    },
    /*{
        to: '/offers',
        name: 'Акции'
    },*/
    {
        to: '/contact',
        name: 'Схема проезда'
    }
])
export const Navigation = (state = initialState, action) => {
    switch (action.type) {

        default:
            return state
    }
}
export function getNavigation(state) {
    return state.Navigation
}

export function getSeoData(location, state) {
    const a = state
        .Navigation
        .find((v) => v.to === location)
    if (a) {
        return a
    } else {
        return state
            .Navigation
            .find((v) => v.isDropdown)
            .DropdownItems
            .find((v) => v.to === location)
    }
}

export function getServiceInfo(state) {
    return state
        .Navigation
        .filter((val) => val.isDropdown)[0]
        .DropdownItems
}


