import React from 'react'
import './About.css'
const About = () => (
    <div className='col-sm d-flex align-items-center About ml-3 mr-3'>
        <div className='d-flex justify-content-center txtNew font_7 '>Приятно осознавать,
            что ты двигаешься к цели каждый день, маленькими шажками, единичными действиями
            в правильном направлении!<br/><br/>
            Нам важно не количество клиентов, а количество их улыбок! Каждая из которых
            выражает приятное удивление.

            <br/><br/>
            Спасибо вам за то, что вы цените чистоту! Ждем ваши улыбки в наших центрах)</div>
    </div>
)
export default About


