import React, {Component} from 'react'
import PropTypes from 'prop-types'
import './About.css'
export default class About extends Component {

    constructor(props) {
        super(props)
        console.log(props)
        switch (props.name) {
            case 'repair':
                this.rend = <AboutRepair/>
                break;
            case 'himchistka':
                this.rend = <AboutHimchistka/>
                break;
            case 'nano':
                this.rend = <AboutNano/>
                break;
            case 'polish':
                this.rend = <AboutPolish/>
                break;
            case 'wash':
                this.rend = <AboutWash/>
                break;
            case 'paint':
                this.rend = <AboutPaint/>
                break;

            default:
                this.rend = <div>
                    Не задано</div>
                break;
        }
    }

    render() {
        return (
            <div className='col-sm-8'>
                <div className='p-3 h-100 About font_7'>
                    {this.rend}
                </div>
            </div>
        )
    }
}

const AboutPaint = (props) => (
    <React.Fragment>
        <span>
            Антигравийная пленка - отличная защита кузова от небольших повреждений которые
            могут возникнуть в ходе эксплуатации автомобиля, от летящих камней, гравия,
            притертостей при небольших ДТП.</span><br/>
        <span>Покрытие автомобиля защитной пленкой позволяет сохранить внешний вид и
            целостность лакокрасочного покрытия и фар, при этом снизить стоимость ремонта
            при локальном повреждении.</span><br/>
        <span>В своей работе мы используем антигравийные пленки Suntek, Hexis и Lumar,
            которые:</span>
        <ul>
            <li>повторяют зеркальность и шагрень ЛКП автомобиля;</li>
            <li>не желтеют от воздействия ультрафиолета;</li>
            <li>не подвержены влиянию температур;</li>
            <li>химически устойчивы по сравнению с другими пленками;</li>
            <li>легко моются и полируются;</li>
            <li>и главное имеют эффект самозатягивания мелких царапин.</li>
        </ul>
        <span>Отличный внешний вид вашему автомобилю на срок от 3 до 5 лет вам обеспечен.
        </span><br/>
        <span>Внимание остерегайтесь подделок! Стоимость оклейки данными пленками не
            может стоить дешевле 25000 только зон риска!</span>
        <br/>
        <span>Фото предоставлено нашими партнерами компанией I-Graphics г. Самара.</span>
    </React.Fragment>
)

const AboutRepair = (props) => (
    <React.Fragment>
        <p>Мы предлагаем уникальную услугу реставрации кожаного салона.</p>
        <span>Мы можем:</span>
        <ul>
            <li>Покрасить и восстановить руль до первозданного состояния</li>
            <li>Покрасить сиденья</li>
            <li>Подобрать любой цвет</li>
            <li>Изменить цвет салона на любой другой</li>
            <li>Устранить порезы, прожоги царапины, а также прокрасы от джинс</li>
        </ul>
        <p>А еще мы умеем восстанавливать пластик от царапин, и восстанавливаем
            оригинальное покрытие soft-touch (мягкий на ощупь пластик, прорезиненное
            покрытие на автомобилях премиум сегмента BMW, Audi, Mercedes, Volkswagen)</p>
        <span>Также мы реставрируем обувь, куртки и кожгалантерею из кожи, замши и нубука</span>
        <p>Вдохните новую жизнь любимым вещам!</p>
    </React.Fragment>
)

const AboutHimchistka = (props) => (
    <React.Fragment>

        Здорово ловить восторженные взгляды прохожих от чистого автомобиля снаружи, но
        стоит задуматься и о чистоте внутри, в первую очередь для себя! Химчистка салона
        автомобиля + устранение неприятных запахов!
        <br/>
        <br/>
        Разочаровались в услугах химчистки? Думаете, что чистоту в салоне возможно
        навести обычным пылесосом?
        <br/>
        Не мучайте себя и своих близких. Любые, даже самые стойкие загрязнения
        подвластны правильной чистке. Затратив минимальное количество времени мы вернем
        Вашему авт омобилю былую чистоту и свежесть, используя при этом только
        проверенные, антиалл ергенные средства, несодержащие хлор.
    </React.Fragment>

)
const AboutNano = (props) => (
    <React.Fragment>

        Неуклюжий сосед поцарапал автомобиль, кустарник рядом с дачей оставил глубокие
        царапины, раздражают круговые царапины от тряпок автомойщиков? Профессиональная
        полировка поможет устранить все эти проблемы! А нанесение защитных керамических
        покрытий воспрепятствуют появлению вышеперечисленных проблем снова! Керамические
        покрытия позволяют в течение нескольких лет наслаждаться блестящим видом
        автомобиля, предотвращают появление царапин, выгорание лакокрасочного покрытия,
        уменьшают загрязнение автомобиля.
        <br/>
        <br/>
        Мы предлагаем своим клиентам продукты лучших производителей, проверенные нами на
        личном опыт, и подберем наилучший вариант нанокерамики и жидкого стекла исходя
        из состояния автомобиля и вашего бюджета.
    </React.Fragment>
)

const AboutPolish = (props) => (
    <React.Fragment>

        <strong>Полировка
        </strong>– процесс поддержания и восстановления идеального состояния автомобиля.<br/>
        <h6>Полировка кузова автомобиля в нашем центре состоит из четырех основных этапов:</h6>
        <ul>
            <li>Подготовительный этап заключается в технической мойке с удалением всех видов
                загрязнений (смола, битум, металлических вкраплений).</li>
            <li>Основной этап. Маскировка и абразивная полировка, с использованием лучших
                полировальных паст и кругов Koch Chemie, Menzerna, Sonax, подкраска сколов.</li>
            <li>Этап антиголограммной полировки с устранением оставшихся мелких царапин.</li>
            <li>Нанесение защитных покрытий.</li>
        </ul>
    </React.Fragment>
)

const AboutWash = (props) => (
    <React.Fragment>

        «Сделайте бомбу» — часто слышим мы от наших постоянных клиентов, и сразу
        понимаем — фирменная мойка Cleantonn. Отвечаем на вопрос «Что в ней
        особенного?»: Все! Начиная от принципов и заканчивая впечатлениями! Согласитесь,
        испытывать чувство восторга, глядя на неповторимый блеск и сияние солнечных
        лучей в отражении своего авто стоит многого. И каждый из нас хочет видеть свой
        автомобиль всегда новым. Но обычно мы видим тусклое мерцание миллиона царапин,
        трудновыводимые пятна от насекомых, смол, бензина. И постепенно новое становится
        старым и невзрачным.
        <br/>
        <br/>
        Предлагаем вам по-новому взглянуть на обыденные вещи! Фирменная мойка Cleantonn
        — это мойка Вашего автомобиля 3-мя уникальными шампунями, благодаря которым
        микроцарапины скрываются, блеск ЛКП насыщается, а автомобиль полностью находится
        под невидимой защитной пленкой. Вы забудете про неотмывшиеся пятна, блеклый цвет
        и постоянно притягивающиеся пыль и грязь! Фирменная мойка "Cleantonn" — ваш
        радостно сияющий взгляд — дело нашего Принципа!
    </React.Fragment>
)

About.propType = {
    name: PropTypes.string.isRequired
}


